import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {
	View,
	Root,
	PanelHeader,
	Panel,
	Cell,
	InfoRow,
	Link,
	Group,
	Div,
	Button,
	Switch,
	Search,
	Select,
	Epic,
	Tabbar,
	ScreenSpinner,
	Spinner,
	Alert,
	TabbarItem,
	HeaderButton,
	platform, FormLayout, SelectMimicry, List,
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home';
import Persik from './panels/Persik';
import Full from './panels/Full';
import Icon24Done from '@vkontakte/icons/dist/24/done';

import Other from './panels/other';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Search from '@vkontakte/icons/dist/24/search';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon28More from '@vkontakte/icons/dist/28/more';
const osname = platform();





class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: "home",
			fetchedUser: null,
            title:"",
            text:"",
			events:[],
            mesto:"",
            stattime:"",
            owner_id:"",
            activeStory: 'feed',
            chatlink:"",
            type:"",
			token:"fd",
			grouplink:"",
			agreed_admin:"",
			your_city:"",
			city:[],
			popout:<ScreenSpinner />,
			test:null,
			types:[],


		};
		this.onStoryChange = this.onStoryChange.bind(this);
	}

	componentDidMount() {

		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppAccessTokenReceived':
					var token= e.detail.data.access_token;
					this.setState({token:token})
					this.load()

					break;

				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 7077031, "scope": ""});

	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })

	};
	load(){
		if(this.state.events.length==0){

			var exp=""
			var re=this.get("https://api.finebot.site/events/events.get?token="+this.state.token)
			re=JSON.parse(re)
			var i =0
			for(i;i<re.response.length;i++){
				console.log("ББББББББ")

				var mesto=re.response[i].country+", "+re.response[i].city+", "+re.response[i].street
				this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.response[i].starttime)+"&&&"+encodeURIComponent(re.response[i].chatlink)+"&&&"+encodeURIComponent(re.response[i].owner_id)+"&&&"+encodeURIComponent(re.response[i].text)+"&&&"+encodeURIComponent(re.response[i].type)+"&&&"+encodeURIComponent(re.response[i].grouplink)+"&&&"+encodeURIComponent(re.response[i].agreed_admin))



			}
			this.setState({your_city:re.your_city,city:re.city,types:re.types})

		}
		this.setState({fetching:false})
		this.setState({popout:null})


		if(this.state.events.length==0){
			this.setState({test:<Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Тут пусто :(</Div>})
			this.openDefault("Ошибка","Мы ничего не нашли в Вашем городе :(")
		}else{
			this.setState({test:""})

		}
		//document.getElementById("test").
	}
	goo(i){

		var title=decodeURIComponent(decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[0]))).split("+").join(" ")
		var mesto=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[1])).split("+").join(" ")
		var datetime=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[2]))
		var chatlink=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[3]))
		var owner_id=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[4]))
		var text=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[5])).split("+").join(" ")
		var type=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[6]))
		var grouplink=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[7]))
		var agreed_admin=decodeURIComponent(decodeURIComponent(this.state.events[i].split("&&&")[8]))
		this.setState({activePanel:"full",title:title,text:text,mesto:mesto,starttime:datetime,owner_id:owner_id,chatlink:chatlink,type:type,grouplink:grouplink,agreed_admin:agreed_admin});
	}
	getOwner(){
		var own="https://vk.com/id"+this.state.owner_id.toString();
		if(this.state.owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{this.state.owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	ret(item, i){

		var title=decodeURIComponent(decodeURIComponent(item.split("&&&")[0])).split("+").join(" ")
		var mesto=decodeURIComponent(item.split("&&&")[1])
		var datetime=decodeURIComponent(item.split("&&&")[2])
		const {go}=this.props
		return(

			<Group >
				<Div>
					<form style={{textAlign:'down',width:'100%',marginBottom:5}} >{title}
					</form>
					<form style={{fontSize:12, textAlign:'top',marginBottom:15}}>{decodeURIComponent(mesto).split("+").join(" ")}<br/>{datetime}
					</form>
					<Button onClick={()=>this.goo(i)} data-to="full">Открыть</Button>
				</Div>


			</Group>

		);
	}
	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story })
	}

	getHeader(){
		if(this.state.activeStory==="discover"){
			return(

				<PanelHeader
				>
				Поиск
				</PanelHeader>



		);
		}else if(this.state.activeStory=="feed"){
			return (
				<PanelHeader>{this.state.your_city}</PanelHeader>
			);
		}
		else{
			return (
				<PanelHeader>Events</PanelHeader>
			);
		}
	}
	closePopout(){
		this.setState({popout:""})
	}
	openDefault (title,text) {
		this.setState({ popout:
				<Alert
					actions={[{
						title: 'ОК',
						autoclose: true,
						style: 'cancel'
					}]}
					onClose={this.closePopout.bind(this)}
				>
					<h2>{title}</h2>
					<p>{text}</p>
				</Alert>
		});
	}
	getty(item,i){
		return(
			<option value={i}>{item}</option>
		);
	}
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",
						fetchedUser: null,
						title:"",
						text:"",
						events:[],
						mesto:"",
						stattime:"",
						owner_id:"",
						activeStory: 'feed',
						chatlink:"",
						type:"",
						token:"fd",
						your_city:"",
						city:[],
						popout:<ScreenSpinner />,
					})
					this.get("https://api.finebot.site/events/account.editCity?token="+this.state.token+"&city="+item)
					this.componentDidMount()

				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}
	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.popout}>

				<Panel id="home">
					{this.getHeader()}

					<Epic activeStory={this.state.activeStory} tabbar={
						<Tabbar>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'feed'}
								data-story="feed"
							><Icon28Newsfeed /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'discover'}
								data-story="discover"
							><Icon28Search /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'messages'}
								data-story="messages"

							><Icon24User /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'notifications'}
								data-story="notifications"
							><Icon28Notifications /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'more'}
								data-story="more"
							><Icon28More /></TabbarItem>
						</Tabbar>
					}>
						<Root id="feed" activePanel="feed">
							<div style={{marginBottom:60}}>{this.state.events.map(this.ret.bind(this))}</div>
							{this.state.test}
						</Root>
						<Root id="discover" activePanel="discover">
							<Search value={this.state.search} onChange={this.onChange}/>
							<Group>
								<List>
									<Cell asideContent={<Switch />}>
										Согласованность с администрацией
									</Cell>

								</List>
								<FormLayout>
									<Select placeholder="Выберите тип">
										{this.state.types.map(this.getty)}
									</Select>
								</FormLayout>
							</Group>

						</Root>
						<Root id="messages" activePanel="messages">

						</Root>
						<Root id="notifications" activePanel="notifications">

						</Root>
						<Root id="more" activeView="more">
							<View id="more">
								<FormLayout>
									<SelectMimicry
										top="Ваш город"
										data-to="other"
										placeholder="Не выбран"
										onClick={this.go}
									>{this.state.your_city}</SelectMimicry>
								</FormLayout>
							</View>
						</Root>
					</Epic>
				</Panel>
				<Full id="full" go={this.go} title={this.state.title} text={this.state.text} mesto={this.state.mesto} starttime={this.state.starttime} agreed_admin={this.state.agreed_admin} grouplink={this.state.grouplink} type={this.state.type} owner_id={this.state.owner_id} chatlink={this.state.chatlink}/>
				<Panel id="other" >
					<PanelHeader>Выбор города</PanelHeader>
					<Group>
						<List>
							{this.state.city.map(this.ret2.bind(this))}
						</List>
					</Group>

				</Panel>

			</View>
		);
	}

}

export default App;
